import Vue from 'vue'
import Router from 'vue-router'
import home from '@/home/home'
import BaoJia from '@/home/index'
import zydetail from '@/components/zszy/detail'
import xcdetail from '@/components/zszy/xcdetail'
import msglist from '@/components/messagelist/header'
import myprofile from '@/components/profile/index'
import myprofileedit from '@/components/profile/profile'
import myprofilesetting from '@/components/profile/setting'
import myxunche from '@/components/xunche/myxunche'
import myzylist from '@/components/zszy/myzy'
import mybaojia from '@/components/baojia/index'
import niubaobao from '@/components/account/niubaobao'
import myorder from '@/components/order/order'
import renzheng from '@/components/profile/renzheng/renzheng'
import tuiguang from '@/components/profile/tuiguang/tuiguang'
import ml from '@/components/profile/4sml/ml'
import huiyuan from '@/components/profile/huiyuan/huiyuan'
import yijianfankui from '@/components/profile/yijianfankui/index'
import xunchezhushou from '@/components/profile/tuijianxunche/index'
import mycompany from '@/components/profile/gongsi/index'
import zhaokehu from '@/components/profile/zhaokehu/index'
import liulanjilu from '@/components/profile/liulanjilu/index'
import niuniugonglue from '@/components/profile/niuniugonglue/index'
import jiaoyitixing from '@/components/order/jiaoyi'
import ziyingcheyuan from '@/components/ziying/index'
import gongsiziliao from '@/components/ziying/detail'
Vue.use(Router)
const router = new Router({
  routes: [
   {
      path: '/',
      name: 'home',
      component:home,
      meta:{
        status:1
      }
    },
    {
      path: '/tg/:phone/:car',
      name: 'homeIndex',
      component: BaoJia
    },
    {
      path:'/zy/detail',
      name:'zydetail',
      component:zydetail,
       meta:{
        status:2
      }
    },
    {
      path:'/zy/xcdetail',
      name:'zyxcdetail',
      component:xcdetail,
      meta:{
        status:2
      }
    },
    {
      path:'/msg/list',
      name:'msglist',
      component:msglist,
      meta:{
        status:1
      }
    },
    {
      path:'/myprofile',
      name:'myprofile',
      component:myprofile,
      meta:{
        status:1
      }
    },
    {
      path:'/myprofile/edit',
      name:'myprofileedit',
      component:myprofileedit,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/setting',
      name:'myprofilesetting',
      component:myprofilesetting,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/renzheng',
      name:'myprofilerenzheng',
      component:renzheng,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/tuiguang',
      name:'myprofiletuiguang',
      component:tuiguang,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/4sml',
      name:'ml',
      component:ml,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/huiyuan',
      name:'huiyuan',
      component:huiyuan,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/yijianfankui',
      name:'yijianfankui',
      component:yijianfankui,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/xunchezhushou',
      name:'xunchezhushou',
      component:xunchezhushou,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/mycompany',
      name:'mycompany',
      component:mycompany,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/zhaokehu',
      name:'zhaokehu',
      component:zhaokehu,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/liulanjilu',
      name:'liulanjilu',
      component:liulanjilu,
      meta:{
        status:2
      }
    },{
      path:'/myprofile/niuniugonglue',
      name:'niuniugonglue',
      component:niuniugonglue,
      meta:{
        status:2
      }
    },
    {
      path:'/myprofile/jiaoyitixing',
      name:'jiaoyitixing',
      component:jiaoyitixing,
      meta:{
        status:2
      }
    },
    {
      path:'/xunche/myxunche',
      name:'myxunche',
      component:myxunche,
      meta:{
        status:2
      }
    },
    {
      path:'/zszy/myzylist',
      name:'myzylist',
      component:myzylist,
      meta:{
        status:2
      }
    },
    {
      path:'/baojia/index',
      name:'mybaojia',
      component:mybaojia,
      meta:{
        status:2
      }
    },
    {
      path:'/myaccount/niubaobao',
      name:'niubaobao',
      component:niubaobao,
      meta:{
        status:2
      }
    },
    {
      path:'/myorder/order',
      name:'myorder',
      component:myorder,
      meta:{
        status:2
      }
    },
      {
        path:'/ziyingcheyuan',
        name:'ziyingcheyuan',
        component:ziyingcheyuan,
        meta:{
          status:2
        }
      },
      {
        path:'/ziyingcheyuan/detail',
        name:'gongsiziliao',
        component:gongsiziliao,
        meta:{
          status:3
        }
      }
    
  ]
});

router.beforeEach((to,from,next)=>{
  let store = this.a.app.$store;
  console.log(to);
  console.log(from);
  if(store){
   
   if(from.meta.status>to.meta.status){
      store.commit('SET_ANIMATE_NAME','vux-pop-out');
    }
    else if(from.meta.status===to.meta.status){
     store.commit('SET_ANIMATE_NAME','vux-pop-no'); 
    }
    else{
     store.commit('SET_ANIMATE_NAME','vux-pop-in'); 
    }
  }
  next();
});
export default router



