<template>
	<div class="myprofile">
		<div class="header bg">
			<span class="back" @click="$router.back(-1);">返回</span>
			<span class="title">设置</span>
			<span class="jb"></span>
		</div>

		<div class="row bg marginbottom10">
			<span class="left">修改密码</span>
			<div class="right">
				<i></i>
			</div>
		</div>
		
		<div class="row bg">
			<span class="left">接收短信通知</span>
			<span class="right">
				<myswitch :isCheck="duanxin"></myswitch>
			</span>
		</div>
		<div class="row bg ">
			<span class="left">新消息声音提醒</span>
			<span class="right">
				<myswitch :isCheck="xinxiaoxi"></myswitch>
			</span>
		</div>
		<div class="row bg">
			<span class="left">消息免打扰</span>
			<span class="right">
				<myswitch :isCheck="xiaoxipb"></myswitch>
			</span>
		</div>
		<div class="row bg">
			<span class="left">群发功能</span>
			<span class="right">
				<myswitch :isCheck="qffunction"></myswitch>
			</span>
		</div>
		<div class="row bg">
			<span class="left">清除聊天记录</span>
			<span class="right">
			</span>
		</div>
		<div class="row bg">
			<span class="left">同步关注列表</span>
			<span class="right">
			</span>
		</div>

	</div>
</template>
<script>
	import myswitch from './switch'
	export default {
		data(){
			return{
				duanxin:false,
				xinxiaoxi:false,
				xiaoxipb:false,
				qffunction:false
			}
		},
		components:{
			myswitch
		}
	}
</script>
<style scoped>
	.myprofile{background: #f1f1f1; width: 100%;height: 100%;
    position: fixed;}
	.header{display: flex;border-bottom: 1px solid #e1e1e1;}
	.header .back{flex:0 0 60px; background: url(./left.gif) no-repeat center top;    background-size: 11px;
    background-position: 0 3px;margin: 14px 10px;
    line-height: 21px;}
    .header .jb{flex:0 0 60px;}
	.header .title{flex:1;
    line-height: 48px;font-weight: 700;}
	.row .left{flex:0 0 200px;text-align: left;padding-left: 10px;}
	.row .right{flex:1;  text-align: left;padding-right: 10px;}
	
	img{display: block; width: 100%;}
	.row{width: 100%;height: 50px;line-height: 50px;display: flex;}
	
    i{display: inline-block;background: url(./right2.gif) no-repeat center top; background-size: 12px;
		width: 10px; height: 50px;float:right;background-position: -1px 16px;}
	.bg{background: #fff;border-bottom: 1px solid #e1e1e1;}
	.bg2{background: #f9f8f6}
	.marginbottom10{    margin-bottom: 15px;}
</style>


