<template>
	<div class="footerwrapper">
	<div class='footer'>
			<a class="home" :class="{active:GetCurrentIndex=='home'}" href="/#/"><i></i><span>首页</span></a>
			<a class="msg" :class="{active:GetCurrentIndex=='msg'}" href="/#/msg/list"><i></i><span>消息</span></a>
			<a class="fb" @click="fabuShow=true" :class="{active:GetCurrentIndex=='fb'}" ><i></i><span style="color:#fff;">发布</span></a>
			<a class="syq" :class="{active:GetCurrentIndex=='syq'}" ><i></i><span>生意圈</span></a>
			<a class="wd" :class="{active:GetCurrentIndex=='wd'}" href="/#/myprofile" ><i></i><span>我的</span></a>
		</div>
		<div class="fabudetail" v-show="fabuShow">
			<div class="rowwrapper">
			<div class="row1">
				<a class="top1 rotateIn">
					<span class="img"><img src="./xunche.gif"></span>
					<span class="title">发布寻车</span>
				</a>
				<a class="top2 fadeInLeft">
					<span class="img"><img src="./ziyuan.gif"></span>
					<span class="title">发布资源</span>
				</a>
				<a class="top3 fadeInUp">
					<span class="img"><img src="./qunfa.gif"></span>
					<span class="title">微信群发升级版</span>
				</a>
			</div>
			<div class="row2">
				<a class="top4 lightSpeedIn">
					<span class="img"><img src="./kaopu.gif"></span>
					<span class="title">靠谱寻车</span>
				</a>
			</div>
			</div>
			<div class="row3" @click="fabuShow=false">
				<a class="top5 rollIn">
					<span class="img"><img src="./guanbi.gif"></span>
				</a>
			</div>
		</div>
	</div>
</template>
<script>
	import animate from 'animate.css'
	export default {
		props:{
			currentIndex:{
				type:String,
				default:'home'
			}
		},
		data() {
			return {
				fabuShow:false
			}
		},
		computed:{
			GetCurrentIndex:function(){
				return this.currentIndex;
			}
		}
	}
</script>
<style scoped>
	.footerwrapper{ }
	.footer{position:fixed; left: 0; bottom: 0; width: 100%;background: #fff; display: flex; width: 100%;background: #fff; }
	.footer a{ flex:1;padding: 10px 0; }
	.footer a.active{ border-bottom: 2px solid #ff8500; }
	.footer a i{ display: block;
    height: 25px; box-sizing:border-box;}
    .footer a span{ box-sizing:border-box; display: block;font-size:1em;color:#a0a0a0; }
	.footer a.home i{background: url(../../resouce/home.gif) no-repeat center top; background-size: 30px 25px;}
	.footer a.fb{    background: #ff8500; border-radius: 10px;}
	.footer a.msg i{ background: url(../../resouce/msg.gif) no-repeat center top; background-size: 30px 25px;}
	.footer a.fb i{ background: url(../../resouce/fb.gif) no-repeat center top; background-size: 30px 25px; }
	.footer a.syq i{ background: url(../../resouce/syq.gif) no-repeat center top; background-size: 30px 25px; }
	.footer a.wd i{ background: url(../../resouce/wd.gif) no-repeat center top; background-size: 30px 25px; }
	.fabudetail{background: #fdfdf3;    position: absolute;
    width: 100%;
    height: 100%;overflow:hidden;
    top: 0;
    left: 0;
    z-index: 2;}
    .rowwrapper{
    	background: #f1fafb;    position: fixed;
    width: 100%;padding: 10px;
    top: 40%;
    left: 0;
    z-index: 3;
    }
	.fabudetail a{flex:1;margin: 10px;}
    .row1,.row2,.row3{display: flex;}
    .row3{       background: #fefdf9;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 95px;
    z-index: 3;}
    .img{    display: inline-block;width: 80px;}
    img{ display: inline-block;width: 100%;
    border-radius: 50%; }
     span.title{display: block;}
     .top1{animation-duration:.3s;animation-delay:0.1s;animation-iteration-count:1;animation-fill-mode:both;}
     .top2{animation-duration:.3s;animation-delay:0.2s;animation-iteration-count:1;animation-fill-mode:both;}
     .top3{animation-duration:.3s;animation-delay:0.3s;animation-iteration-count:1;animation-fill-mode:both;}
     .top4{animation-duration:.3s;animation-delay:0.2s;animation-iteration-count:1;animation-fill-mode:both;}
     .top5{animation-duration:.3s;animation-delay:0.1s;animation-iteration-count:1;animation-fill-mode:both;}
</style>


