<template>
	<div class="wrapper">
		<div class="main">
		<div class="header">
			<div class="searchwrapper" style="display:none;">
			<div class="search">
				<input type="search" class="searchinput" placeholder="输入车型/品牌/指导价/配置等">
				<span class="kfcall">客服</span>
			</div>
			</div>
			<mybanner :SwiperType="SwiperType"></mybanner>
		</div>
		<div class="centerWrapper">
			<div class="itemlist">
				<div class="row">
					<a class="item">
						<div class="itemImg">
							<img src="../../resouce/xc.gif"/>
						</div>
						<p>寻车</p>
					</a>
					<a class="item">
						<div class="itemImg">
							<img src="../../resouce/zggc.gif"/>
						</div>
						<p>中规国产</p>
					</a>
					<a class="item">
						<div class="itemImg">
							<img src="../../resouce/pxjk.gif"/>
						</div>
						<p>平行进口</p>
					</a>
				</div>
				<div class="row">
					<a class="item" href="/#/myprofile/4sml">
						<div class="itemImg">
							<img src="../../resouce/4sml.gif"/>
						</div>
						<p>4S名录</p>
					</a>
					<a class="item">
						<div class="itemImg">
							<img src="../../resouce/nnwl.gif"/>
						</div>
						<p>牛牛物流</p>
					</a>
					<a class="item" href="/#/ziyingcheyuan">
						<div class="itemImg">
							<img src="../../resouce/zycy.jpg"/>
						</div>
						<p>自营车源</p>
					</a>
				</div>
			</div>
			<div class="tab">
				<div class="box">
					<img src="../../resouce/bmx5.jpg">
				</div>
				<div class="box" style="border-right: none;" >
					<img src="../../resouce/ftll.jpg">
				</div>
			</div>
			<div class="tab">
				<div class="box">
					<img src="../../resouce/bkgl8209.jpg">
				</div>
				<div class="box" style="border-right: none;">
					<img src="../../resouce/xdsntb.jpg">
				</div>
			</div>
			<div class="tabxc">
				<div class="box" :class="{active:current=='zsxc'}" @click="toggleTab('zsxc')">
					<a>专属寻车</a>
					<span class="line"></span>
				</div>
				<div class="box" :class="{active:current=='zszy'}" @click="toggleTab('zszy')">
					<a>专属资源</a>
				</div>
				
			</div>
			<transition name="component-fade">
				<component :is="current"></component>
			</transition>
			<myfooter :currentIndex="footerIndex"></myfooter>
		</div>
		</div>
		
	</div>
</template>
<script>
    import mybanner from '../components/CommonBanner'
    import myfooter from '../components/footer'
    import zsxc from '../components/zsxc'
    import zszy from '../components/zszy'
	export default {
		data() {
			return{
				SwiperType:'slide',
				current:'',
				footerIndex:'home'
			}
		},
		mounted(){
			this.toggleTab('zsxc');
			this.$nextTick(()=>{
				/*测试vue 挂载全局函数属性*/
				this.logstrtest();	
			});
			
		},
		methods:{
			toggleTab:function(a){
				this.current=a;
			}
		},
		components:{
			mybanner,myfooter,zsxc,zszy
		}
	}
</script>
<style scoped>
	*{ margin:0; padding:0; }
	.wrapper{ width: 100%;height: 100%;position: relative; }
	.main{padding-bottom:150px;background: #fff;}
	.header{ }
	.searchwrapper{position: relative;width: 90%;margin: 0px auto;z-index: 10;
    top: 70px; }
	.search{position: absolute; width: 100%;}
	.search input{width: 88%; height: 70px;line-height: 70px;    border: none;
    border-radius: 10px;float:left;box-sizing:border-box;    padding-left: 30px;
    font-size: 30px;
    font-weight: 700;}
	.search .kfcall{ background: url(../../resouce/call.png) no-repeat; display: inline-block; width: 12%; height: 70px; line-height: 70px;background-position: 10px 14px; color: #fff;     padding-left: 36px;
    background-size: 36px 36px; float:left;box-sizing:border-box;
    font-size: 30px;
    font-weight: 700;}
    .banner li{font-size: 0;}
	.banner img{ display: block;width: 100%;  }
	.centerWrapper{}
	.itemlist{ display: flex;flex-wrap: wrap;margin-top: 20px;}
	.itemlist .row{ display: flex; flex-basis: 100%; justify-content: space-around; }
	.itemlist .item{   }
	.itemImg{ padding: 0 35px; }
	.itemlist .item span{ display: block;     width: 139px;height: 100px;}
	.itemlist .item p{  color: #848484;line-height: 28px;height: 28px;font-size:1em;margin: 10px 0; }
	.itemlist .item img{ display: block; width: 100%; }
	.itemlist .item .xc{ background: url(../../resouce/xc.gif) no-repeat center top; }
	.itemlist .item .zggc{background: url(../../resouce/zggc.gif) no-repeat center top;}
	.itemlist .item .pxjk{background: url(../../resouce/pxjk.gif) no-repeat center top;}
	.itemlist .item .ml{background: url(../../resouce/4sml.gif) no-repeat center top;}
	.itemlist .item .nnwl{background: url(../../resouce/nnwl.gif) no-repeat center top;}
	.itemlist .item .zycy{background: url(../../resouce/zycy.jpg) no-repeat center top;}
	.tab{ display: flex; }
	.tab .box{box-sizing:border-box; width: 50%;float: left; border-right:1px solid #f1f1f1;border-bottom: 12px solid #f1f1f1; }
	.tab .box img{ display: block; width: 100%; }
	.bmx5{background: url(../../resouce/bmx5.jpg) no-repeat center top;height: 170px;}
	.ftll1268{background: url(../../resouce/ftll.jpg) no-repeat center top;height: 170px;}
	.bkgl8{background: url(../../resouce/bkgl8209.jpg) no-repeat center top;height: 170px;}
	.xdtns8{background: url(../../resouce/xdsntb.jpg) no-repeat center top;height: 170px;}
	
	.tabxc .box{ box-sizing:border-box;width: 50%; float: left;    margin-top: 5px;
	    line-height: 50px;font-size: 1em;font-weight: 700;border-bottom: 2px solid #e1e1e1; }
	    .line{ display: inline-block; border-right: 1px solid #e1e1e1;float: right;     height: 30px;
    margin: 10px 0; }
	
	.tabxc .box.active{ border-bottom: 2px solid #ffb15c; }
	.tabxc .box.active a{color :#ffb15c;}
	.router-link-exact-active{ text-decoration: none;color :#ffb15c; }
	.component-fade-enter-active, .component-fade-leave-active {
	  transition: opacity .3s ease;
	}
	.component-fade-enter, .component-fade-leave-active {
	  opacity: 0;
	}
</style>


