<template>
	<div class="detailwrapper">
		<vheader>资源详情</vheader>
		<mybanner :SwiperType="SwiperType"></mybanner>
		<div class="contentwrapper">
			<div class="title"><p>纳瑞 16款 1.4L 自动领先型 GLX 929</p></div>
			<div class=" desc">
				<div class="updateDate">更新时间：09.29</div>
				<div class="price">指导价：9.29万/下1.9万</div>
			</div>
			<div class="row"><a href="" class="cxcs">车型参数<i></i></a></div>
			<div class="row">
				<span class="box">期望成交价:</span><span class="qwprice">7.39万</span>
				<span class="kpj">4000</span>
			</div>
			<div class="row">
				<span class="box">规格参数</span>
				<span class="box">国产/现车</span>
			</div>
			<div class="row">
				<span class="box">外观装饰</span>
				<span class="box">优雅白/不限</span>
			</div>
			<div class="row">
				<span class="box">车辆所在地</span>
				<span class="box">牛牛全国各大库</span>
			</div>
			<div class="row">
				<span class="box">销售区域</span>
				<span class="box">全国</span>
			</div>
			<div class="row">
				<span>备注</span>
			</div>
			<div class="row">
				<div class="remark"><p>成板接车包物流 价格详谈</p>
					<p>全国招区域代理和包销合作</p>
					<p>代理商包销商价格更诱人</p>
					<p>详情请咨询牛牛各区域经理</p>
				</div>
			</div>
		</div>
		<vfooter></vfooter>
	</div>
</template>
<script>
	import vheader from './header'
	import vfooter from './foot'
	import mybanner from '../CommonBanner'
	export default {
		data () {
			return{
				SwiperType:'slide'
			}
		},
		components:{
			vheader,vfooter,mybanner
		}
	}
</script>
<style scoped>
	.detailwrapper{ width: 100%;height: 100%;  }
	.banner img{ display: block; width: 100%; }
	.box{box-sizing: border-box; width:30%; float:left;}
	.contentwrapper{font-size:14px;  margin: 0px 10px;text-align: left;}
	.title{}
	.title p{ font-size: 1.2em; line-height: 3em; font-weight: 700; }
	.desc{ line-height: 3em; }
	.desc .updateDate{box-sizing: border-box; width:40%; float:left;}
	.desc .price{box-sizing: border-box; width:55%; float:left; text-align: right; padding-right: 10px;}
	.cxcs{}
	.kpj{background: url(./kpjbg.gif) no-repeat center top; display: inline-block; width: 100px; height: 30px;background-size: 100px;
	font-weight: 700;
    color: red;padding-left: 45px;transform:rotate(-8deg);    padding-top: 2px;float: right; }
	.row{ border-bottom: 1px solid #f2f2f2; line-height: 3em; clear: both;display: flex;}
	.qwprice{ color: #ff8501; font-weight: 700; }
	.remark{ flex: 1; width: 100%; }
	
</style>


