<template>
	<div>
		<div class="switch" :class="{active:isActive}" @click="toggoTab()">
							<div class="btn"></div>
		</div>
	</div>
</template>
<script>
	export default{
		props:{
			isCheck:{
				type:Boolean,
				default:false
			}
		},
		data() {
			return {
				isActive:false
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.isActive=this.isCheck;
			});
		},
		methods:{
			toggoTab:function () {
			    this.isActive=!this.isActive;
			}
		}
	}
</script>
<style scoped>
	.switch{    position: relative;
    width: 70px;
    height: 30px;
    border: 1px solid #e1e1e1;
    background: #fff;
    border-radius: 20px;    float: right;
    margin: 10px 0;
    outline：none
    transition-duration: 0.2s;
}
    .switch.active{
    	background: #ff8500;
    	transition-duration: 0.2s;
    }
	.switch.active:before{ content:'on';right: auto;
    position: absolute;
    box-sizing: border-box;
        width: 90px;
    height: 30px;
    line-height: 30px;
    left: 15px; color: #fff;font-weight: 400; }
	.switch:after{ content:''; }
	.switch .btn{ position: absolute;
    width: 30px;
    height: 30px;
    border: 1px solid #e1e1e1;
    background: #f3f3f3;
    border-radius: 50%;
    top: -1px; box-sizing:border-box;  box-shadow: 0 2px 5px rgba(0,0,0,.4); 
    outline：none
    transition-duration: 0.2s;
    transform: translate(0px, 0px);}
    .switch.active .btn{transition-duration: 0.2s;
    transform: translate(43px, 0px);}
</style>


