<template>
	<div class="myxunche">
		<div class="header bg">
			<span class="back" @click="$router.back(-1);">返回</span>
			<span class="title">我的资源</span>
			<span class="jb"></span>
		</div>
		<div class="tab">
			<div class="item" :class="{active:currentcomponet=='myzylist'}" @click="toggleTab('myzylist')">
			在售资源<span></span></div>
			<div class="item" :class="{active:currentcomponet=='myzylist2'}" @click="toggleTab('myzylist2')">已下架<span></span></div>
		</div>
		<component :is="currentcomponet"></component>
		
	</div>
</template>
<script>
	import myzylist from './myzylist'
	import myzylist2 from './deletezy'
	export default {
		data() {
			return {
				currentcomponet:'',
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.toggleTab('myzylist');	
			});
			
		},
		methods:{
			toggleTab(a){
				this.currentcomponet=a;
			}
		},
		components:{
			myzylist,myzylist2
		}
	}
</script>
<style scoped>
	.myxunche{width: 100%;height: 100%;
    position: absolute;background: #fff;}
	.header{display: flex;border-bottom: 1px solid #e1e1e1;}
	.header .back{flex:0 0 60px; background: url(../profile/left.gif) no-repeat center top;    background-size: 11px;
    background-position: 0 3px;margin: 14px 10px;
    line-height: 21px;}
    .header .jb{flex:0 0 60px;}
	.header .title{flex:1;
    line-height: 48px;font-weight: 700;}
    .tab{ display:flex; }
    .tab .item{flex:1; line-height: 30px; height: 50px; line-height: 50px;border-bottom: 1px solid #e1e1e1;
    }
    .tab .item.active{border-bottom: 3px solid #ff8500;}
    .tab .item span{border-right: 1px solid #e1e1e1;    float: right;
    display: inline-block;
    width: 1px;
    height: 30px;
    margin: 10px 0;}

    
</style>


