<template>
	<div class="main">
	<div class="header">
				<div class="tab">
					<div class="box" :class="{active:current=='msglist'}" @click="toggleTab('msglist')"><p>消息</p></div>
					<div class="box" :class="{active:current=='mygz'}" @click="toggleTab('mygz')"><p>我的关注</p></div>
				</div>
    </div>
    <component :is="current"></component>
    <myfooter :currentIndex='footerIndex'></myfooter>
    </div>
</template>
<script>
	import msglist from './msglist'
	import mygz from './mygz'
	import myfooter from '../../components/footer'
	export default {
		data() {
			return {
				current:'msglist',
				footerIndex:'msg'
			}
		},
		mounted(){
			this.toggleTab('msglist');
		},
		methods:{
			toggleTab:function(a){
				this.current=a;
			}
		},
		components:{
			msglist,mygz,myfooter
		}
	}
</script>
<style scoped>
	.header{margin:13px auto 20px; width: 65%;line-height: 30px;font-weight: 700;}
	.tab{border:1px solid  #ff8500;background: #fff; display: flex;border-radius: 4px;}
	.box{ box-sizing:border-box;width:50%;float: left; }
	.box.active{ background: #ff8500; color:#fff; }
</style>


