<template>
	<div class="banner">
		<!-- Slider main container -->
		<div class="swiper-container">
		    <!-- Additional required wrapper -->
		    <div class="swiper-wrapper">
		        <!-- Slides -->
		        <div class="swiper-slide">
		        	<a href=""><img src="../../resouce/banner.gif"></a>
		        </div>
		        <div class="swiper-slide">
		        	<a href=""><img src="../../resouce/banner.gif"></a>
		        </div>
		        <div class="swiper-slide">
		        	<a href=""><img src="../../resouce/banner.gif"></a>
		        </div>
		    </div>
		    <!-- If we need pagination -->
		    <div class="swiper-pagination"></div>
		</div>
	</div>
</template>
<script>
	import Swiper from '../../resouce/swiper/swiper-3.3.1.min.js'
	require('../../resouce/swiper/swiper-3.3.1.min.css')
	export default {
		props:{
			SwiperType:{
				type:String,
				default:'slide'
			}
		},
		mounted() {
			    this.$nextTick(()=>{
			    	this.Swiper();
			    })
		},
		methods:{
			Swiper() {
				var mySwiper = new Swiper('.swiper-container', {
			    // Optional parameters
			    direction: 'horizontal',
			    loop: true,
			    autoplay:2000,
			    autoplayDisableOnInteraction:false,
			    speed:1000,
			    effect: this.SwiperType,
			    // If we need pagination
			    pagination: '.swiper-pagination'
			  })    
			}
		}
	}
	
</script>
<style scoped>
	.swiper-container{  }
	.swiper-container img {width: 100%;display: block;}
</style>


