<template>
	<div class="myxunche">
		<div class="header bg">
			<span class="back" @click="$router.back(-1);">返回</span>
			<span class="title">我的寻车</span>
			<span class="jb"></span>
		</div>
		<div class="tab">
			<div class="item" :class="{active:currentcomponet=='xunchelist'}" @click="toggleTab('xunchelist')">
			寻车中(0)<span></span></div>
			<div class="item" :class="{active:currentcomponet=='xunchelist2'}" @click="toggleTab('xunchelist2')">交易中(2)<span></span></div>
			<div class="item" :class="{active:currentcomponet=='xunchelist3'}" @click="toggleTab('xunchelist3')">全部(2)</div>
		</div>
		<component :is="currentcomponet"></component>
		<div class="footer">
			<a class="kp">
				<i></i>
				<span>靠谱寻车</span>
			</a>
			<a class="pt" style="border-right:none;">
				<i></i>
				<span>普通寻车</span>
			</a>
		</div>	
	</div>
</template>
<script>
	import xunchelist from './xunchelist'
	import xunchelist2 from './xunchelist'
	import xunchelist3 from './xunchelist'
	export default {
		data() {
			return {
				currentcomponet:'',
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.toggleTab('xunchelist');	
				window.srcoll(0,0);
			});
			
		},
		methods:{
			toggleTab(a){
				this.currentcomponet=a;
			}
		},
		components:{
			xunchelist,xunchelist2,xunchelist3
		}
	}
</script>
<style scoped>
	.myxunche{width: 100%;height: 100%;
    position: absolute;background: #fff;}
	.header{display: flex;border-bottom: 1px solid #e1e1e1;}
	.header .back{flex:0 0 60px; background: url(../profile/left.gif) no-repeat center top;    background-size: 11px;
    background-position: 0 3px;margin: 14px 10px;
    line-height: 21px;}
    .header .jb{flex:0 0 60px;}
	.header .title{flex:1;
    line-height: 48px;font-weight: 700;}
    .tab{ display:flex; }
    .tab .item{flex:1; line-height: 30px; height: 50px; line-height: 50px;border-bottom: 1px solid #e1e1e1;
    }
    .tab .item.active{border-bottom: 3px solid #ff8500;}
    .tab .item span{border-right: 1px solid #e1e1e1;    float: right;
    display: inline-block;
    width: 1px;
    height: 30px;
    margin: 10px 0;}
    .footer{position: fixed;left:0;bottom: 0;width: 100%;height: 50px;background: #ff8500;}
    .footer a{ box-sizing:border-box; display: block;width: 50%;float:left; color: #fff; 
    	line-height: 50px;height: 50px;border-right: 1px solid #fff; }
   	a span{margin: 13px 0 0 0;
    display: inline-block;}
   	a.kp i{ background: url(./kaopuxunche.gif) no-repeat; height: 30px;
    width: 49px;
    position: absolute;
    background-size: 28px;
    background-position: 16px 4px;}
   	a.pt i{background: url(./xunche.gif) no-repeat;  height: 30px;
    width: 49px;
    position: absolute;
    background-size: 28px;
    background-position: 16px 4px;}
    
</style>


