<template>
	<div class="myxunche">
		<div class="header bg">
			<span class="back" @click="$router.back(-1);">返回</span>
			<span class="title">浏览记录</span>
			<span class="jb"></span>
		</div>
		<div class="wrapper">
			<div class="title">
				<span>今天</span>
			</div>
			<zszy></zszy>
		</div>
	</div>
</template>
<script>
	import zszy from '../../zszy'
	export default{
		components:{
			zszy
		}
	}
</script>
<style scoped>
	.myxunche{background: #eeeeee;
    position: absolute;
    width: 100%;
    height: 100%;}
    .bg{background: #fff;}
	.header{display: flex;border-bottom: 1px solid #e1e1e1;}
	.header .back{flex:0 0 60px; background: url(../left.gif) no-repeat center top;    background-size: 11px;
    background-position: 0 3px;margin: 14px 10px;
    line-height: 18px;}
    .header .jb{flex:0 0 60px;}
	.header .title{flex:1;
    line-height: 48px;font-weight: 700;}
    .wrapper .title{    float: left;
        padding: 5px 10px;
    font-size: 14px;
    font-weight: 700;}
    
</style>


